import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/login/login'
import Index from '@/pages/index/index'
import Register from '@/pages/register/register'
import Account from '@/pages/account/account';
import Result from '@/pages/result/result';
import Detail from '@/pages/detail/detail';
import Pay from '@/pages/pay/pay';
import Confirm from '@/pages/pay/confirmInfo.vue';
import qrPay  from '@/pages/pay/qrPay.vue';
import success  from '@/pages/pay/success.vue';
import Main from '@/pages/index/main'
Vue.use(Router)




export default new Router({
  //定义路由集
routes: [
  {
    path: '/',
    name: 'Main',
    component: Main,
    children: [
      {
        path: '/',
        name: 'Index',
        component: Index
      },
      {
        path: '/index/:name',
        name: 'logined',
        component: Index
      },
      {
        path: '/login',
        name: 'Login',
        component: Login
      },{
        path: '/register',
        name: 'Register',
        component: Register
      },{
        path: '/account',
        name: 'Account',
        component: Account
      },{
        path: '/result',
        name: 'Result',
        component: Result
      },{
        path: '/result/:w',
        name: 'ResultTo',
        component: Result
      },{
        path: '/detail/:w',
        name: 'Detail',
        component: Detail,
      },
      
      {
        path: '/confirm',
        name: 'Confirm',
        component: Confirm
      },{
        path: '/qrpay',
        name: 'qrPay',
        component: qrPay,
      },
      ,{
        path: '/success',
        name: 'success',
        component: success,
      }
    ]
  },{
    path: '/pay',
    name: 'Pay',
    component: Pay,
  }
]
  
})



