.wrap {
    width: 1200px;
    margin: 0 auto;
}
.title{
    margin: 5px 50px;
    border-bottom: 1px solid #eee;
}
.bacc {
    background-color: #fff;
}
.list{
    padding: 0px;
}
.num {
    color: red;
}
.selector{
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
}
.key{
    font:bolder;
    font-size: 16px;
}
.tag{
    padding: 0px 20px;
    text-decoration: none;
    color: rgb(0, 0, 0);
}
