import * as datas from './data';
var Mock = require('mockjs')


/* 主页商品分类接口 开始 */

Mock.mock('/index/menu','get',() => {
    return datas.indexMenu;
})
//轮播数据
Mock.mock('/index/menu/banner','get',() => {
    return datas.indexBanner;
})
//即将开售 //推荐
Mock.mock('/index/tabs','get',() => {
    return datas.tabData;
})
//展示楼层
Mock.mock('/index/floor','get',() => {
    return datas.floorData;
})

//热门
Mock.mock('/index/hot','get',() => {
    return datas.hotList;
})
//test
Mock.mock('/login','get',() => {
    return datas.phoneLogin;
})
//accountInfo
Mock.mock('/account','get',() => {
    return datas.accountInfo;
})
//users
Mock.mock('/account/users','get',() => {
    return datas.users;
})
//订单管理
Mock.mock('/account/orders','get',() => {
    return datas.orders;
})



//列表页
Mock.mock('/result','get',() => {
    return datas.results;
})
//侧边推荐
Mock.mock('/sider','get',() => {
    return datas.sider;
})
//详情页
Mock.mock('/detail','get',() => {
    return datas.detail;
})

Mock.mock('/seats','get',() => {
    return datas.seats;
})



//测试
Mock.mock(RegExp('/index/testGet' + ".*"),'get',() => {
    return datas.phoneLogin;
})

Mock.mock('/index/testPost','post',() => {
    return datas.phoneLogin;
})



