// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';

import ViewUI from 'view-design';
import 'view-design/dist/styles/iview.css';
import './common/iview-theme/index.less';
import './assets/iconfont/iconfont.js';
import App from './App';//放到样式后，这样样式顺序才能覆盖iview默认样式
import router from './router'
import axios from 'axios';
import mock from './common/mock/mock';
import * as http from './common/http/http';
import global from './common/global'
import toDetail from './common/toDetail';

Vue.config.devtools = true;
Vue.prototype.$http = http
Vue.prototype.$axios = axios
Vue.prototype.$toDetail = toDetail
Vue.config.productionTip = false
Vue.prototype.GLOBAL = global

Vue.use(ViewUI)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  
  mounted(){
    // console.log("main js >");
    // console.log(global);

  }
})



