<template>
  <div class="wrap">
      <span>个人主页 ></span>
      <span>{{menuGroup}} ></span>
      <span>{{menu}}</span>
  </div>
</template>

<script>
export default {
    name:'pageHeader',
    props:['menuName'],
    data:function(){
        return {
            menuGroup:'交易中心',
            menu:''
        }
    },
    methods:{
        getMenuName(){
            // console.log(this.menuName);
            if(this.menuName=='orderManager'){
                this.menuGroup =  "交易中心";
                this.menu = "订单管理"
            }else if(this.menuName=='accountManage'){
                this.menuGroup ="账户中心";
                this.menu = "个人信息"
            }else if(this.menuName=='users'){
                this.menuGroup ="账户中心";
                this.menu = "常用购票人"
            }
        }
    },
    mounted:function (){
        // console.log("updated");
        this.getMenuName()
    }
}
</script>

<style scoped>
.wrap{
    margin: 0px 0px 20px 0px;
}
</style>


