var toDetail = function(param) {
    if(this.$route.params.w==param){
        this.$Notice.error({
            title: '已经这个页面',
            duration:0.75
        });
    }else{
        //成功，但是数据不刷新   问题：当在同一页面改变路由参数时，页面会出现不刷新的情况
        //使用watch属性监听route对象 写在detail页面
        // watch: {
        //     $route(to, from) {
        //       // 对路由变化作出响应...
        //       //可以使用$router.go(0)进行刷新,（补充，该方法在ios手机上无效）
        //       //可以改用window.location.reload()
        //     }
        // }
        
        this.$router.push({name:'Detail', params: { w:param }})
        this.$Notice.success({
            title: '成功',
            duration:0.75
        });
    }
}
    
export default toDetail;


